@import url(https://fonts.googleapis.com/css?family=Merienda&display=swap);.d-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-gap:40px}
.super-card{display:block;background-color:#fff;border-radius:6px;-webkit-box-shadow:0 0 16px -2px rgba(99,108,231,0.2);box-shadow:0 0 16px -2px rgba(99,108,231,0.2);padding:12px}
.super-card:hover{text-decoration:none}
.super-card .super-card-head .badge{padding:8px 8px;color:#ff6868;background-color:#ffe9e9}
.super-card .super-card-head .title{margin-top:10px;color:#445996;font-weight:bold}
.super-card .super-card-body{color:#7580a2}
.super-card .super-card-foot{padding:12px 0}
.text-mainColor{color:#525ce5}
.text-mainLightColor{color:#ccd2e3}
.text-mainDeepColor{color:#223a80}
body{padding:20px;background-color:#f7f9ff;font-family:'Merienda', 'Microsoft JhengHei', cursive}
.sidebar{border-radius:6px;width:300px;min-height:600px;display:block;padding:0 20px;-ms-flex-negative:0;flex-shrink:0}
@media screen and (max-width: 767px){.sidebar{width:100%}}
.sidebar ul{margin-top:20px;padding-left:0}
.sidebar ul li{list-style:none}
.sidebar ul li a{padding:15px 20px;border-radius:0.375rem;color:#ccd2e3;display:-webkit-box !important;display:-ms-flexbox !important;display:flex !important;-ms-flex-wrap:nowrap;flex-wrap:nowrap;text-decoration:none}
.sidebar ul li a.active{display:block;background:-webkit-gradient(linear, left bottom, left top, from(#5761e6), to(#747ced));background:linear-gradient(0deg, #5761e6, #747ced)}
.main{-webkit-box-flex:1;-ms-flex-positive:1;flex-grow:1;-ms-flex-negative:1;flex-shrink:1}
.main .header{width:100%}
.super-title{color:#445996;font-size:15px;font-weight:bold}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
